import React, { useState } from 'react';
import { MDBBtn } from 'mdb-react-ui-kit';
import ReportHeader from './ReportHeader';
import ReportRow from './ReportRow';
import ReportSignatures from './ReportSignatures';
import './Report.css';
import axios from 'axios'; // Add axios for API requests

const Report = ({ data, language }) => {
  const [isTranslated, setIsTranslated] = useState(false);
  const [translatedData, setTranslatedData] = useState(data);

  const handleBackToHome = () => {
    window.location.reload(); // Reload the page to go back to the form
  };

  const handlePrint = () => {
    const originalContent = document.body.innerHTML; // Save the original content
    const printContent = document.querySelector('.report-container').outerHTML; // Get the printable content
    document.body.innerHTML = printContent; // Replace the body content with the printable content
    window.print(); // Trigger the print dialog
    document.body.innerHTML = originalContent; // Restore the original content
  };

  const handleEmail = () => {
    const subject = 'Eyewitness Report';
    const body = `
      Eyewitness Report\n
      Date: ${data.date}\n
      Project Name: ${data.projectName}\n
      Injured Party Name: ${data.injuredPartyName}\n
      Eyewitness Full Name: ${data.eyewitness}\n
      Address: ${data.address}\n
      Phone Number: ${data.phoneNumber}\n
      Company Name: ${data.companyName}\n
      Position: ${data.position}\n
      Incident Type: ${data.incidentType.accident ? 'Accident ' : ''}${data.incidentType.injury ? 'Injury ' : ''}${data.incidentType.propertyDamage ? 'Property Damage' : ''}\n
      Date of Incident: ${data.incidentDate}\n
      Time of Incident: ${data.incidentTime}\n
      Conditions: ${data.conditions}\n
      Cause and Events: ${data.causeAndEvents}\n
    `;
    window.location.href = `mailto:webber@example.com?subject=${encodeURIComponent(subject)}&body=${encodeURIComponent(body)}`;
  };

  const handleToggleTranslation = async () => {
    if (!isTranslated) {
      try {
        const newTranslatedData = { ...data };
        for (const key in data) {
          if (typeof data[key] === 'string' && data[key].trim() !== '') {
            const response = await axios.get('https://api.mymemory.translated.net/get', {
              params: {
                q: data[key],
                langpair: 'en|es',
              },
            });
            newTranslatedData[key] = response.data.responseData.translatedText;
          }
        }
        setTranslatedData(newTranslatedData);
        setIsTranslated(true);
      } catch (error) {
        console.error('Error translating to Spanish:', error);
      }
    } else {
      setTranslatedData(data); // Revert to original data
      setIsTranslated(false);
    }
  };

  return (
    <>
      <div className="report-container">
        <div className="container report-border">
          <ReportHeader />
          <div className="report-row">
            <ReportRow id="date" label="Date" value={translatedData.date || 'N/A'} isTwoColumn />
            <ReportRow id="projectName" label={language === 'es' ? 'Nombre del Proyecto' : 'Project Name'} value={translatedData.projectName || 'N/A'} align="2" isTwoColumn />
          </div>
          <ReportRow id="injuredPartyName" label="Injured Party Name" value={translatedData.injuredPartyName || 'N/A'} />
          <ReportRow id="eyewitnessFullName" label="Eyewitness Full Name" value={translatedData.eyewitness || 'N/A'} />
          <ReportRow id="address" label="Address" value={translatedData.address || 'N/A'} />
          <ReportRow id="phoneNumber" label="Phone Number" value={translatedData.phoneNumber || 'N/A'} />
          <div className="report-row">
            <ReportRow id="companyName" label="Company Name" value={translatedData.companyName || 'N/A'} isTwoColumn />
            <ReportRow id="position" label="Position" value={translatedData.position || 'N/A'} align="2" isTwoColumn />
          </div>
          <ReportRow id="incidentType" label="Incident Type" value={`${translatedData.incidentType.accident ? 'Accident ' : ''}${translatedData.incidentType.injury ? 'Injury ' : ''}${translatedData.incidentType.propertyDamage ? 'Property Damage' : ''}`} />
          <div className="report-row">
            <ReportRow id="incidentDate" label="Date of Incident" value={translatedData.incidentDate || 'N/A'} isTwoColumn />
            <ReportRow id="incidentTime" label="Time of Incident" value={translatedData.incidentTime || 'N/A'} align="2" isTwoColumn />
          </div>
          <ReportRow id="conditions" label="Conditions" value={translatedData.conditions || 'N/A'} />
          <ReportRow id="causeAndEvents" label="Cause and Events" value={translatedData.causeAndEvents || 'N/A'} />
          <ReportSignatures id="eyewitnessSignature" label="Eyewitness Signature" signature={translatedData.eyewitnessSignature} />
          <ReportSignatures id="representativeSignature" label="Webber Waterworks Representative Signature" signature={translatedData.representativeSignature} />
        </div>
      </div>
      <div className="print-buttons" style={{ textAlign: 'center', marginTop: '5%', marginBottom: '3%', width: '100%' }}>
        <MDBBtn onClick={handlePrint}>Print</MDBBtn>
        <MDBBtn onClick={handleEmail}>Email</MDBBtn>
        <MDBBtn onClick={handleBackToHome}>Back to Home</MDBBtn>
        <MDBBtn onClick={handleToggleTranslation}>{isTranslated ? 'Show Original' : 'Translate to Español'}</MDBBtn>
      </div>
    </>
  );
};

export default Report;